@mixin flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin input {
  margin: 5px;

  width: 300px;
  height: 30px;
  font-size: 20px;

  outline: none;
  padding-left: 10px;

  border-width: 0px 0px 2px 0px;
  border-radius: 10px;
         
  &:focus{
    background-color: #ced1bde3;
  }

  &:hover{
    background-color: #faf3e0ea;
    transition: 0.3s;
  }
}

@mixin label {
  font-size: 15px;
  font-weight: bold;
}

.app {
  display: flex;
  background-color: rgb(230, 228, 227);

  height: 100vh;

  align-items: center;
  flex-direction: column;

  .preview, .setting{
    display: flex;
    flex-direction: column;

    width: 1200px;
  }

  .preview{

    @include flex-center;

    padding-top: 30px;

    height: 40%;
    background-color: rgb(252, 227, 231);

    .preview_canvas{
      height: 60%;
    }

    .preview_btn{

      @include flex-center;

      height: 40%;
      flex-direction: column;

      label{
        @include label;
      }
      input {
        @include input;
        &.filename{
          width: 365px;
        }
      }
      button {

        padding: 15px;

        min-width: 200px;
        min-height: 30px;

        background-color: #434F78;
        color: white;
        font-family: "Open Sans", sans-serif;
        font-size: larger;

        border: none;
        border-radius: 30px;
        box-shadow: 2px 2px 10px 2px gray;
        
        &:hover{
          background-color: #66729b;
          transition: 0.3s;
        }
  
        &:active{
          background-color: #ffbf00b9;
        }
  
        &:focus{
          outline-color: rgb(80, 83, 119);
          outline-style: solid 2px;
        }

      }
      
    }

  }

  .setting{
    height: 60%;
    background-color: rgb(222, 247, 243);

    .setting_text{

      @include flex-center;

      height: 30%;
      background-color: beige;
      display: flex;
      flex-direction: column; 

      label{
        @include label;
      }
      

      input {
        @include input;
        
        &.text{
          width: 450px;
        }
      
        &.size{
          width: 50px;
        }
      
        &.color{
          width: 100px;
        }
      
      }
    }

    .setting_color{

      @include flex-center;

      height: 70%;
      background-color: rgb(205, 240, 208);

      display: flex;
      // flex-direction: column;
      
    }
  }
}

